// app/layout.js
import './globals.css';

import TelegramInit from './components/TelegramInit'; // Импортируем TelegramInit

export const metadata = {
  title: 'My Awesome App',
  description: 'Example Next.js 13 layout',
};

export default function RootLayout({ children }) {
  return (
    <html lang="en">
      <body>
        {/* Инициализация Telegram Web App */}
        <TelegramInit />

        {/* Шапка сайта */}
      

        <div style={{ display: 'flex', minHeight: '80vh' }}>
          {/* Левая панель (например, навигация) */}

          {/* Основной контент (куда подставляются страницы) */}
          <main style={{ flex: 1, padding: '1rem' }}>{children}</main>

          {/* Правая панель (например, чат, реклама, виджеты) */}
        </div>

        {/* Футер сайта */}
      </body>
    </html>
  );
}